.headerLogo {
	height: 50px;
	width: auto;
	transition: 0.2s;
}

.navbar {
	box-shadow: #000 0 3px 5px;
}

main {
	padding-top: 5em;
	background-color: #003153;
}


#serviceArticle {


} 

#serviceImage {
	height: 500px;
	background-color: #000;
	background-image: url("http://devfecta.com/images/web-development.jpg");
	background-size: cover;
	background-repeat: no-repeat;
	opacity: 0.4;
	z-index: 0;
	position: fixed;
	top: 0;
	left: 0;
	padding: 0;
	box-shadow: #000 0px 3px 5px;
	
}

#serviceIntro {
	position: relative;
	font-size: 1.5em;
	min-height: 300px;
	height: auto;
	padding: 2em 0 -2em 0;
}
#serviceIntro p {
	text-shadow: #000 2px 2px 3px;
}

#portfolioArticle {
	background-color: #666;
	padding: 2em 0;
	min-height: 50px;
	box-shadow: #222 0 -3px 5px;

} 

.btn-primary {
	background-color: #003153;
	border: #003153 solid 1px;
}

.btn-primary:hover {
	background-color: #001e33;
	border: #003153 solid 1px;
}

footer {
	background-color: #333;
	padding: 1em;
}

.serviceCard {
	box-shadow: #000 3px 3px 5px;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
